.sourceHandle,
.targetHandle {
  background-color: #fff;
  border-radius: 12px;
  width: 12px;
  height: 12px;
  box-shadow: 1px 2px 10px -1px hsla(225, 93%, 12%, 0.12);
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center;
  &.hidden {
    // background: transparent;
    opacity: 0;
    box-shadow: none;
    // width: 0;
    // height: 0;
  }
  .icon {
    width: 12px;
    height: 12px;
    color: #999999;
    pointer-events: none;
  }
  &.handleHover {
    background-color: #5582f3;
    .icon {
      color: #fff;
    }
  }
  &.sourceHandleLeft {
    .icon {
      transform: scale(1) rotate(90deg);
    }
  }
  &.sourceHandleBottom {
    .icon {
      transform: scale(1) rotate(0deg);
    }
  }

  &.sourceHandleRight {
    .icon {
      transform: scale(1) rotate(-90deg);
    }
  }
  &.targetHandleTop {
    .icon {
      transform: scale(1) rotate(0deg);
    }
  }
}

.wrapper {
  height: 50px;
  width: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 0.375rem;
  border-width: 1px;
  border-style: solid;
  border-color: #e5e7eb;
  background-color: #fff;
  padding-left: 18px;
  padding-right: 18px;
  vertical-align: middle;
  box-shadow:
    0 0 #0000,
    0 0 #0000,
    0 1px 2px 0 #0000000d;
  box-sizing: border-box;
  .content {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    font-size: 14px;
    &::before {
      margin-right: 12px;
    }
    .name {
      display: inline-block;
      padding-left: 6px;
      overflow: auto;
      flex: 1;
    }
    svg {
      color: black;
      width: 15px;
      height: 15px;
      g,
      path {
        fill: black;
      }
    }
  }
  .error {
    position: absolute;
    right: 0;
    display: flex;
    color: #fff;
    width: 20px;
    height: 20px;
    background-color: #ef4444;
    border-radius: 20px;
    justify-content: center;
    align-items: center;
    user-select: none;
    transform: translate(30px, 0) rotate(0) skew(0) skewY(0) scaleX(1) scaleY(1);
    .errorText {
      position: absolute;
      left: 30px;
      color: #000;
      font-size: 14px;
      padding-top: 8px;
      padding-right: 12px;
      padding-left: 12px;
      padding-bottom: 8px;
      background-color: #fff;
      white-space: nowrap;
    }
  }
}

.wrapperImage {
  display: flex;
  align-items: center;
  padding: 4px 8px;
  width: 240px;
  height: 50px;
  margin-bottom: 20px;
  // user-select: none;
  border: 1px solid #e5e5e5;
  border-radius: 6px;
  box-sizing: border-box;
  background-color: #fff;
  padding-left: 18px;
  padding-right: 18px;
  box-shadow:
    0 0 #0000,
    0 0 #0000,
    0 1px 2px 0 #0000000d;
  &:hover {
    border: 1px solid #5582f3;
  }
  &.notDraggable {
    cursor: not-allowed;
    background-color: #f1f5f9;
  }
  .content {
    display: flex;
    align-items: center;
    width: 100%;
    height: 100%;
    font-size: 14px;
    &::before {
      margin-right: 12px;
    }
    .name {
      display: inline-block;
      padding-left: 6px;
      overflow: auto;
      flex: 1;
    }
    svg {
      color: black;
      width: 15px;
      height: 15px;
      g,
      path {
        fill: black;
      }
    }
  }
}
