.gaiFlow {
  position: relative;
  display: flex;
  height: 100%;
  width: 100%;
  flex-direction: row;

  .mainFlow {
    margin-left: 280px;
    position: relative;
    flex: 1 1 0%;
    .loading {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 100%;
      height: 100%;
      .loadingImg {
        width: 96px;
        height: 96px;
      }
    }
    .flowContent {
      display: block;
      background-color: #fff;
      height: 100%;
      &.hidden {
        display: node;
      }
    }
  }
}
