.panel {
  position: absolute;
  background-color: #fff;
  width: 280px;
  height: 100%;
  margin: 0;
  top: 0;
  .title {
    width: 100%;
    height: 40px;
    background: #f2f2f2;
    padding: 8px 12px;
    box-sizing: border-box;
    .text {
      color: #276ff5;
      background: #fff;
      border-radius: 4px;
      font-size: 12px;
      display: inline-block;
      padding: 4px 8px;
    }
  }
  .panelContent {
    display: flex;
    padding: 20px;
    align-items: center;
    flex-direction: column;
  }
}
